.label-switch {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  border-radius: 16px;
  box-sizing: border-box;
  height: 20px;
  position: relative;
  cursor: pointer;
  -ms-flex-item-align: center;
  -webkit-align-self: center;
  align-self: center;
}
.label-switch input[type="checkbox"] {
  display: none;
  z-index: 10;
}
.label-switch .checkbox {
  width: 40px;
  height: 20px;
  border-radius: 16px;
  box-sizing: border-box;
  background: #fff;
  z-index: 0;
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border: 1px solid #e2e4ea;
  cursor: pointer;
  position: relative;
  -webkit-transition-duration: 300ms;
  transition-duration: 300ms;
}
.label-switch .checkbox:before {
  content: ' ';
  position: absolute;
  left: 2px;
